import { Component, h, State } from '@stencil/core';
/**
 * Text editor in HTML mode, with XSS attack in the initial value.
 */
@Component({
    tag: 'limel-example-text-editor-with-html-xss',
    shadow: true,
})
export class TextEditorWithHtmlXssExample {
    @State()
    private value: string =
        '<p><em>Hello</em>, <strong>html</strong>!</p><p>Here comes the nasty: <img src="x" onerror="console.log(\'XSS from html\')" /> <script>console.log(\'hacked from html!\')</script></p>';

    @State()
    private readonly = false;

    public render() {
        return [
            <limel-text-editor
                value={this.value}
                onChange={this.handleChange}
                readonly={this.readonly}
                contentType="html"
            />,
            <limel-example-controls>
                <limel-checkbox
                    checked={this.readonly}
                    label="Readonly"
                    onChange={this.setReadonly}
                />
            </limel-example-controls>,
            <limel-example-value value={this.value} />,
        ];
    }

    private setReadonly = (event: CustomEvent<boolean>) => {
        event.stopPropagation();
        this.readonly = event.detail;
    };

    private handleChange = (event: CustomEvent<string>) => {
        this.value = event.detail;
    };
}
